import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from "app/@uisftech/common/service/http.constant";
@Component({
  selector: 'app-custom-mind-escrow',
  templateUrl: './custom-mind-escrow.component.html',
  styleUrls: ['./custom-mind-escrow.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class CustomMindEscrowComponent implements OnInit {

  Property1: boolean = true;
  Property2: boolean = false;
  Property3: boolean = false;
  Property4: boolean = false;
  Property5: boolean = false;
  Property6: boolean = false;
  custType: string = '';
  files: any = [];
  fileList: any = [];//上传文件列表
  fileUrl: string;
  fileName: string;
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;
  UPLOAD: string = TZB_CUS_UPLOAD;
  //提示信息
  msgs: Message[] = [];
  //模态框的标题
  headerTitle: string = "修改";
  //模态框是否显示
  display: boolean = false;
  showModel: string;
  //参数项
  detailValue: string;

  tabMesg: any = tableMessage;// 表格无数据时显示的信息

  constructor(
    public commonHttpService: CommonHttpService,
    public cusOperationHttpService: CusOperationHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public router: Router,
    public fb: FormBuilder,
  ) {

  }

  ngOnInit() {
    this.custType = '1';
  }

  //上传
  load(event) {
    //   // 
    //   for (let file of event.files) {
    //     this.files.push(file)
    //   }
    //   if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
    //     this.fileList = JSON.parse(event.xhr.response);
    //   }
    //   let param = { fileList: this.fileList }
    //   this.cusOperationHttpService.transferCustBatchImport(param).subscribe(data => {
    //   })
    // }
    for (let file of event.files) {
      this.files.push(file)
    }
    //上传成功 后台返回的 文件路径 和文件名
    if (event.xhr.response) {
      this.headerTitle = '客户批量代管';
      this.display = true;
      this.showModel = '1';
      this.fileList = JSON.parse(event.xhr.response);
      this.detailValue = this.fileList;
    }
  }

  //查询
  doQuery() {

  }

  //重置
  doReset() {

  }

  //代管
  doEscrow() {

  }

  //取消
  doCancel() {

  }

  //日期
  showData() {

  }

  //更改查询范围
  changeCustType(param) {
    this.doEmpty();
    if (param == '1') {
      this.Property1 = true;
    }
    if (param == '2') {
      this.Property2 = true;
    }
    if (param == '3') {
      this.Property3 = true;
    }
    if (param == '4') {
      this.Property4 = true;
    }
    if (param == '5') {
      this.Property5 = true;
    }
    if (param == '6') {
      this.Property6 = true;
    }
  }

  //清空
  doEmpty() {
    this.Property1 = false;
    this.Property2 = false;
    this.Property3 = false;
    this.Property4 = false;
    this.Property5 = false;
    this.Property6 = false;
  }
}
