import { Component, OnInit,Input,Output,EventEmitter } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import {CALENDAR_CN} from 'app/@uisftech/common/constant/config';
import { Router } from '@angular/router'
@Component({
  selector: 'app-department-me-escrow-cust-num',
  templateUrl: './department-me-escrow-cust-num.component.html',
  styleUrls: ['./department-me-escrow-cust-num.component.scss'],
  providers:[Commfunc]
})
export class DepartmentMeEscrowCustNumComponent implements OnInit {
  @Input() inValue: string;  //传入的值
  @Output() outValue: EventEmitter<any> = new EventEmitter();  //出参
  items3;
  constructor(
    public router:Router,
    public commfunc:Commfunc
  ) { }

  ngOnInit() {
  }
  // 客户号跳转360
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }
}
