export class CustomBatchEscrowBean {
    entrustManageCustBatchImportInfo:any[];	//客户批量代管文件导入信息
    custNo:string;	                        //客户号
    custName:string;	                    //客户名称
    oldOrgId:string;	                    //被代管人
    oldEmpNo:string;	                    //被代管人机构
    entrustManageEmpNo:string;	            //代管人
    entrustManageOrgId:string;	            //代管人机构
    entrustDateFrom:string;	                //代管开始日期
    entrustDateTo:string;	                //代管结束日期


    entrustManageCustBatchImportFail:any[];	//客户批量代管文件导入错误
    errno:string;	                        //错误码
    errMsg:string;	                        //错误信息
 
    //显示批量上传内容
    pageNum:number;                         //当前页
    pageSize:number;                        //每页条数

    fileName:string;                        //文件名
    fileUrl:string;                         //文件路径
}