import { Component, OnInit } from '@angular/core';
import { SelectItem, Message, ConfirmationService } from 'primeng/primeng';
import { CustomMeEscrowBean } from './bean/customMeEscrow.bean';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Observable } from 'rxjs/Rx';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Router } from '@angular/router';
import { CommonHttpService } from '../../../../../common/http/common.http.service';
@Component({
  selector: 'app-custom-me-escrow',
  templateUrl: './custom-me-escrow.component.html',
  styleUrls: ['./custom-me-escrow.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class CustomMeEscrowComponent implements OnInit {
  
  tabMesg: any = tableMessage;// 表格无数据时显示的信息
  cities: SelectItem[];
  selectedCity: string;
  items1: any;
  items2: any;
  total: any;
  entrustToEmpName: any =[];	    //代管人姓名
  entrustStatus: any = [];	        //代管状态
  pageNum: number;	              //当前页数
  pageSize: number;	            //每页条数
  msgs: Message[] = [];         //提示信息
  display: boolean = false;     //详情
  myEntrustInfo: any[];	        //我的代管信息
  showModel: string;
  checkValue: any = [];
  ch: any = CALENDAR_CN;
  temData: Date = new Date();
  temData1: Date = new Date();
  entrustDateFrom: any;     	//代管开始日期
  entrustDateTo: any;	        //代管结束日期
  applyDateFrom: any;	        //申请开始日期
  applyDateTo: any;	            //申请结束日期
  customMeEscrowBean: CustomMeEscrowBean = new CustomMeEscrowBean()
  orgId: string;//commoheader机构id
  peopleList: any;//接收柜员信息
  //获取所有码值
  code:any;

  constructor(
    public cusOperationHttpService: CusOperationHttpService,
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public router: Router
  ) {
    let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.customMeEscrowBean.entrustFromEmpName = ch.userName;
    this.codeValues();
    this.entrustToEmpName=this.code['EntrustToEmpName'];
    this.entrustStatus=this.code['EntrustStatus']
  }

  ngOnInit() {
    this.customMeEscrowBean.pageSize = 10;
    this.customMeEscrowBean.pageNum = 1;
    // this.query();
    // this.queryTeller()
  }
//码值
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
  }
  //日期
  showData() {
    this.temData = this.entrustDateFrom;

  }

  //代管人信息查询
  queryTeller() {
    //根据机构号查询机构人员queryTellerByCombConditions
    let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.orgId = ch.orgList[0].orgId;
    let param = {
      orgId: this.orgId,
      pageSize: 1000000
    }
    this.commonHttpService.queryTellerByCombConditions(param).subscribe(data => {
      if (data.tellerList) {
        this.peopleList = this.commfunc.listToLabelvalue(data.tellerList, 'tellerName', 'tellerId');
        this.customMeEscrowBean.entrustToEmpName = this.peopleList.tellerName;
        this.customMeEscrowBean.entrustToEmpNo = this.peopleList.tellerId;

      }
    })
  }
  // 初始查询
  query() {
    this.customMeEscrowBean.entrustDateFrom = this.commfunc.transDateN(this.entrustDateFrom);
    this.customMeEscrowBean.entrustDateTo = this.commfunc.transDateN(this.entrustDateTo);
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.customMeEscrowBean.entrustFromEmpNo = ch['userId'];
    this.cusOperationHttpService.myEntrustQryForCustManager(this.customMeEscrowBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items1 = data.myEntrustInfo;
        this.total = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  // 查询按钮
  doQuery() {
    this.customMeEscrowBean.entrustDateFrom = this.commfunc.transDateN(this.entrustDateFrom);
    this.customMeEscrowBean.entrustDateTo = this.commfunc.transDateN(this.entrustDateTo);
    this.customMeEscrowBean.applyDateFrom = this.commfunc.transDateN(this.applyDateFrom);
    this.customMeEscrowBean.applyDateTo = this.commfunc.transDateN(this.applyDateTo);
    this.cusOperationHttpService.myEntrustQryForCustManager(this.customMeEscrowBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items1 = data.myEntrustInfo;
        this.total = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  // 重置-按钮
  doReset() {
    this.customMeEscrowBean = new CustomMeEscrowBean();
    this.display = true;
  }

  //分页
  paginate(event) {
    this.customMeEscrowBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.customMeEscrowBean.pageNum = currentPage;
    this.doQuery()
    this.customMeEscrowBean.pageNum = 1;
  }
  //客户号跳转360
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }
}
