import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-escrow-branch-manager',
  templateUrl: './escrow-branch-manager.component.html',
  styleUrls: ['./escrow-branch-manager.component.scss']
})
export class EscrowBranchManager implements OnInit {
  nav1:boolean=true;
  nav2:boolean=false;
  constructor() { }

  ngOnInit() {
  }
  linkfirst(){
    this.nav1=true;
    this.nav2=false;
  }
  linksecond(){
    this.nav1=false;
    this.nav2=true;
  }
}
