import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-escrow-account-manager',
  templateUrl: './escrow-account-manager.component.html',
  styleUrls: ['./escrow-account-manager.component.scss']
})
export class EscrowAccountManager implements OnInit {
  nav1:boolean=true;
  nav2:boolean=false;
  nav3:boolean=false;
  constructor() { }

  ngOnInit() {
  }
  linkfirst(){
    this.nav1=true;
    this.nav2=false;
    this.nav3=false;
  }
  linksecond(){
    this.nav1=false;
    this.nav2=true;
    this.nav3=false;
  }
  linkthird(){
    this.nav1=false;
    this.nav2=false;
    this.nav3=true;
  }
}
