import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SelectItem, Message, ConfirmationService } from 'primeng/primeng';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { EntrustManagerWaitingApproveQryBean } from '../department-pending-escrow/bean/departmentPendingEscrow.bean';
import { DepartmentPendingEscrowDetailBean } from '../department-pending-escrow/bean/departmentPendingEscrowDetail.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN ,tableMessage} from 'app/@uisftech/common/constant/config';
import { Router } from '@angular/router';
import { CommonHttpService } from '../../../../../common/http/common.http.service';

@Component({
  selector: 'app-department-approved-escrow',
  templateUrl: './department-approved-escrow.component.html',
  styleUrls: ['./department-approved-escrow.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class DepartmentApprovedEscrowComponent implements OnInit {
  @Input() inValue: string;  //传入的值
  @Output() outValue: EventEmitter<any> = new EventEmitter();  //出参
  tabMesg: any = tableMessage;// 表格无数据时显示的信息
  cities: SelectItem[];
  selectedCity: string;
  title1: string = '客户查询结果';
  entrustDateFrom: any;	//代管开始日期
  entrustDateTo: any;	//代管结束日期
  applyDateFrom: any;	//申请开始日期
  applyDateTo: any;	//申请结束日期
  ch: any = CALENDAR_CN;
  temData: Date = new Date();
  addUpHeader: any;//dialog 头
  lookOver: boolean = false;//查看 dialog
  items: any=[];
  departmentStatus=[];
  items2: any;
  total: number;
  msgs: Message[] = [];   //提示信息
  showModel: string;  //组件的显示
  display: true; //
  approvedDetailQryInfo: any[];
  entrustApplyStatus: any = [];//申请状态
  peopleList: any; //接收柜员信息
  orgId: string;//代管人被代管人机构id
  //获取所有码值
code:any;
  entrustManagerWaitingApproveQryBean: EntrustManagerWaitingApproveQryBean = new EntrustManagerWaitingApproveQryBean()
  departmentPendingEscrowDetailBean: DepartmentPendingEscrowDetailBean = new DepartmentPendingEscrowDetailBean()
  constructor(
    public cusOperationHttpService: CusOperationHttpService,
    public commonHttpService: CommonHttpService,
    public router: Router,
    public commfunc: Commfunc
  ) { 
    this.codeValues()
    this.departmentStatus=this.code['DepartmentStatus'];
    this.entrustApplyStatus=this.code['Status']
  }

  ngOnInit() {
    this.entrustManagerWaitingApproveQryBean.pageSize = 10;
    this.entrustManagerWaitingApproveQryBean.pageNum = 1;
    this.departmentPendingEscrowDetailBean.pageSize = 10;
    this.departmentPendingEscrowDetailBean.pageNum = 1;
    this.query();
    this.queryTeller()
  }
  //码值
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
  }
  //代管人信息查询
  queryTeller() {
    //根据机构号查询机构人员queryTellerByCombConditions
    // 
    let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.orgId = ch.orgList[0].orgId;
    let param = {
      orgId: this.orgId,
      pageSize: 1000000
    }
    this.commonHttpService.queryTellerByCombConditions(param).subscribe(data => {
      if (data.tellerList) {
        this.peopleList = this.commfunc.listToLabelvalue(data.tellerList, 'tellerName', 'tellerId');
        this.entrustManagerWaitingApproveQryBean.entrustToEmpName = this.peopleList.tellerName;
        this.entrustManagerWaitingApproveQryBean.entrustToEmpNo = this.peopleList.tellerId;
        this.entrustManagerWaitingApproveQryBean.entrustFromEmpName = this.peopleList.tellerName;
        this.entrustManagerWaitingApproveQryBean.entrustFromEmpNo = this.peopleList.tellerId;
      }
    })
  }
  //初始查询
  query() {
    // this.entrustManagerWaitingApproveQryBean.entrustDateFrom = this.commfunc.transDateN(this.entrustDateFrom);
    // this.entrustManagerWaitingApproveQryBean.entrustDateTo = this.commfunc.transDateN(this.entrustDateTo);
    // this.entrustManagerWaitingApproveQryBean.applyDateFrom = this.commfunc.transDateN(this.applyDateFrom);
    // this.entrustManagerWaitingApproveQryBean.applyDateTo = this.commfunc.transDateN(this.applyDateTo);
    // this.entrustManagerWaitingApproveQryBean.entrustApplyStatus = ''
    this.cusOperationHttpService.entrustManagerQry(this.entrustManagerWaitingApproveQryBean).subscribe(data => {
      if (data.returnCode) {
        if (data.returnCode.code == 'success') {
          this.items = data.myEntrustInfo1;
          this.total = data.totalNum
          // this.msgs = [];
          // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message })
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
        }
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

  }
  // 查询按钮
  queryBtn() {
    this.entrustManagerWaitingApproveQryBean.entrustDateFrom = this.commfunc.transDateN(this.entrustDateFrom);
    this.entrustManagerWaitingApproveQryBean.entrustDateTo = this.commfunc.transDateN(this.entrustDateTo);
    this.entrustManagerWaitingApproveQryBean.applyDateFrom = this.commfunc.transDateN(this.applyDateFrom);
    this.entrustManagerWaitingApproveQryBean.applyDateTo = this.commfunc.transDateN(this.applyDateTo);
    // this.entrustManagerWaitingApproveQryBean.entrustApplyStatus = '1'
    this.cusOperationHttpService.entrustManagerQry(this.entrustManagerWaitingApproveQryBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items = data.myEntrustInfo1;
        this.total = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

    })
  }
  // 客户数查看
  responNum(col) {
    this.addUpHeader = '客户查看详情';
    this.lookOver = true;
    col['pageSize'] = this.departmentPendingEscrowDetailBean.pageSize;
    col['pageNum'] = this.departmentPendingEscrowDetailBean.pageNum;
    // this.detailQry(col)
    let param = {
      entrustApplyId: "001",
      pageNum: 1,
      pageSize: 5
    }
    // ;
    let ob = this.cusOperationHttpService.entrustManagerApprovedDetailQry(param);
    ob.subscribe(data => {
      if (data.returnCode) {
        if (data.returnCode.code == 'success') {
          this.items2 = data.approvedDetailQryInfo;
          this.total = data.totalNum;
          // this.msgs = [];
          // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message })
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
        }
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  // 重置按钮
  resetBtn() {
    this.entrustManagerWaitingApproveQryBean = new EntrustManagerWaitingApproveQryBean();
  }
  //分页
  paginate(event) {
    // ;
    //每页显示的条数
    this.entrustManagerWaitingApproveQryBean.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.entrustManagerWaitingApproveQryBean.pageNum = currentPage;
    //调用查询的方法
    this.queryBtn()
    this.entrustManagerWaitingApproveQryBean.pageNum = 1;
  }
  // 客户号跳转360
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }
  //日期
  showData() {
    this.temData = this.applyDateFrom;
  }
  paginates (event) {

  }
}
