import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CustomMeEscrowDetailBean } from '../bean/customMeEscrowDetail.bean';
import { CusOperationHttpService } from '../../../../../http/custom-operation/custom-operation.http.service'
import { SelectItem, Message, ConfirmationService } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'app-custom-me-escrow-detail',
  templateUrl: './custom-me-escrow-detail.component.html',
  styleUrls: ['./custom-me-escrow-detail.component.scss'],
  providers: [CusOperationHttpService],
})

export class CustomMeEscrowDetailComponent implements OnInit {
  tabMesg: any = tableMessage;// 表格无数据时显示的信息
  display: boolean = false;     //模态框

  showModel: string;            //1 更新 2添加 3详情

  headerTitle: string;          //弹出框的头信息
  items2: any;                  //表数据
  total: any;                   //总页数
  customMeEscrowBean: CustomMeEscrowDetailBean = new CustomMeEscrowDetailBean();
  msgs: Message[] = [];         //提示信息
  @Input() inValue: string;     //传入的值
  @Output() outValue: EventEmitter<any> = new EventEmitter();  //出参
  constructor(
    public cusOperationHttpService: CusOperationHttpService,
  ) { }

  ngOnInit() {
    this.query()
  }
  // 查询
  query() {
    let param = {}
    this.cusOperationHttpService.myEntrustDetailQryCustManager(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items2 = data.myEntrustDetailQryInfo;
        this.total = data.totalPages;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
}
