import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../../http/custom-operation/custom-operation.http.service';
import { TZB_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Message } from 'primeng/primeng';
import { CustomBatchEscrowBean } from '../bean/customBatchEscrow.bean';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'app-escrow-batch-detail',
  templateUrl: './escrow-batch-detail.component.html',
  styleUrls: ['./escrow-batch-detail.component.scss']
})
export class EscrowBatchDetailComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  customBatchEscrowBean:CustomBatchEscrowBean = new CustomBatchEscrowBean();  
  //接收后台返回数据
  tableData: any;
  //接收后台返回的总条数
  total: any;
  //提示信息
  msgs: Message[] = [];
  //接收父级带过来的fileName和fileUrl
  customBatchUpdetail: any = [];
  //
  fileMessageList: any;
  tabMesg: any = tableMessage;// 表格无数据时显示的信息
  constructor(
    public cusOperationHttpService:CusOperationHttpService
  ) { }

  ngOnInit() {
  }
  ngOnChanges() {
    this.customBatchEscrowBean.pageNum = 1;
    this.customBatchEscrowBean.pageSize = 10;
    if (this.inValue) {
      this.inValue.forEach(item => {
        this.customBatchEscrowBean.fileName = item.fileName;
        this.customBatchEscrowBean.fileUrl = item.fileUrl;
      })
    }
    this.query();
  }
  fuzhi() {
    this.customBatchUpdetail = this.inValue;
  }
  //页面初始查询
  query() {
    this.cusOperationHttpService.entrustManageCustBatch(this.customBatchEscrowBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.batchFileImportInfo
        this.total = data.totalNum
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  // //代管事件
  // applyBatch(event) {
  //   this.cusOperationHttpService.entrustManageCustBatch(this.customBatchEscrowBean).subscribe(data => {
  //     if (data.returnCode.code == 'success') {
  //       this.msgs = [];
  //       this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
  //     } else {
  //       this.msgs = [];
  //       this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
  //     }
  //   }, error => {
  //     this.msgs = [];
  //     this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
  //   })
  // }
  //客户批量代管
  escrow() {
    let obj = {}
    this.inValue.forEach(item => {
      obj['fileName'] = item.fileName
      obj['fileUrl'] = item.fileUrl
    })
    this.cusOperationHttpService.entrustManageCust(obj).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: '代管成功' });
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.customBatchEscrowBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.customBatchEscrowBean.pageNum = currentPage;
    //调用查询的方法
    this.query();
    this.customBatchEscrowBean.pageNum = 1;
  }
  //取消
  cancel() {
    this.outValue.emit(false);
  }
}
