import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
/**
 * 客户代管
 * 
 *
 */
//客户代管
import { CustomEscrow } from './custom-escrow.component';//客户代管
import { EscrowAccountManager } from './escrow-account-manager/escrow-account-manager.component';//客户代管客户经理
import { CustomMindEscrowComponent } from './escrow-account-manager/custom-mind-escrow/custom-mind-escrow.component';//客户代管发起
import { CustomBatchEscrowComponent } from './escrow-account-manager/custom-batch-escrow/custom-batch-escrow.component';//客户批量代管
import { CustomMeEscrowComponent } from './escrow-account-manager/custom-me-escrow/custom-me-escrow.component';//我的代管
import { EscrowDepartmentManager } from './escrow-department-manager/escrow-department-manager.component';//部门主管
import { DepartmentMindEscrowComponent } from './escrow-department-manager/department-mind-escrow/department-mind-escrow.component';//部门主管客户代管发起
import { DepartmentBatchEscrowComponent } from './escrow-department-manager/department-batch-escrow/department-batch-escrow.component';//部门主管客户批量代管
import { DepartmentMeEscrowComponent } from './escrow-department-manager/department-me-escrow/department-me-escrow.component';//部门主管我的代管
import { DepartmentPendingEscrowComponent } from './escrow-department-manager/department-pending-escrow/department-pending-escrow.component';//部门主管待审批代管
import { DepartmentApprovedEscrowComponent } from './escrow-department-manager/department-approved-escrow/department-approved-escrow.component';//部门主管已审批代管
import { EscrowBranchManager } from './escrow-branch-manager/escrow-branch-manager.component';//上级有权人
import { BranchPendingEscrowComponent } from './escrow-branch-manager/branch-pending-escrow/branch-pending-escrow.component';//上级有权人待审批
import { BranchApprovedEscrowComponent } from './escrow-branch-manager/branch-approved-escrow/branch-approved-escrow.component';//上级有权人已审批

export const routes: Routes = [

    {
        path: '',
        component: CustomEscrow,
        children: [
            {
                path: 'escrow-account-manager',
                component: EscrowAccountManager,
                children: [

                    {
                        path: '', redirectTo: 'custom-mind-escrow', component: CustomMindEscrowComponent
                    },
                    {
                        path: 'custom-mind-escrow', component: CustomMindEscrowComponent
                    },
                    {
                        path: 'custom-batch-escrow', component: CustomBatchEscrowComponent
                    },
                    {
                        path: 'custom-me-escrow', component: CustomMeEscrowComponent
                    }
                ]
            },
            {
                path: 'escrow-department-manager',
                component: EscrowDepartmentManager,
                children: [
                    {
                        path: '', redirectTo: 'department-pending-escrow', component: DepartmentPendingEscrowComponent
                    },
                    {
                        path: 'department-mind-escrow', component: DepartmentMindEscrowComponent
                    },
                    {
                        path: 'department-batch-escrow', component: DepartmentBatchEscrowComponent
                    },
                    {
                        path: 'department-me-escrow', component: DepartmentMeEscrowComponent
                    },
                    {
                        path: 'department-pending-escrow', component: DepartmentPendingEscrowComponent
                    },
                    {
                        path: 'department-approved-escrow', component: DepartmentApprovedEscrowComponent
                    }
                ]
            },
            {
                path: 'escrow-branch-manager',
                component: EscrowBranchManager,
                children: [
                    {
                        path: '', redirectTo: 'branch-pending-escrow', component: BranchPendingEscrowComponent
                    },
                    {
                        path: 'branch-pending-escrow', component: BranchPendingEscrowComponent
                    },
                    {
                        path: 'branch-approved-escrow', component: BranchApprovedEscrowComponent
                    }
                ]
            }
        ]
    },

]

export const CusEscrowRouting: ModuleWithProviders = RouterModule.forChild(routes);
