import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SelectItem, Message, ConfirmationService } from 'primeng/primeng';
import { DepartmentMeEscrowBean } from './bean/departmentMeEscrow.bean';
import { DepartmentMeEscrowReleaseBean } from './bean/departmentMeEscrowRelease.bean'
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'app-department-me-escrow',
  templateUrl: './department-me-escrow.component.html',
  styleUrls: ['./department-me-escrow.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class DepartmentMeEscrowComponent implements OnInit {
  @Input() inValue: string;  //传入的值
  @Output() outValue: EventEmitter<any> = new EventEmitter();  //出参
  cities: SelectItem[];
  selectedCity: string;
  items1: any;
  items2: any;
  items3: any;
  entrustDateFrom: any;	//代管开始日期
  entrustDateTo: any;	//代管结束日期
  applyDateFrom: any;	//申请开始日期
  applyDateTo: any;	//申请结束日期
  ch: any = CALENDAR_CN;
  temData: Date = new Date();
  temData2: Date = new Date()
  showModel: string;  //组件的显示
  display: true; //
  total: string;
  msgs: Message[] = [];               //提示信息
  confirmDisplay: boolean = false; //直接分配按钮模态框显示
  entrustStatus: any = [];//申领状态

  departmentMeEscrowBean: DepartmentMeEscrowBean = new DepartmentMeEscrowBean()
  departmentMeEscrowReleaseBean: DepartmentMeEscrowReleaseBean = new DepartmentMeEscrowReleaseBean()
  entrustToEmpName;
  code:any;
  constructor(
    public cusOperationHttpService: CusOperationHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.departmentMeEscrowBean.entrustFromEmpName = ch.userName;
    this.codeValues();
    this.entrustStatus=this.code['Status']
  }

  ngOnInit() {
    this.departmentMeEscrowBean.pageSize = 10;
    this.departmentMeEscrowBean.pageNum = 1;
    this.query()
  }
//码值
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
  }
  // 初始查询
  query() {
    let param = {}
    this.departmentMeEscrowBean.entrustDateFrom = this.commfunc.transDateN(this.entrustDateFrom);
    this.departmentMeEscrowBean.entrustDateTo = this.commfunc.transDateN(this.entrustDateTo);
    this.departmentMeEscrowBean.applyDateFrom = this.commfunc.transDateN(this.applyDateFrom);
    this.departmentMeEscrowBean.applyDateTo = this.commfunc.transDateN(this.applyDateTo);
    this.cusOperationHttpService.myEntrustQryForDepManager(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items1 = data.myEntrustInfo;
        this.total = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {

      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //点击查询和初始查询
  doQuery() {
    this.departmentMeEscrowBean.entrustDateFrom = this.commfunc.transDateN(this.entrustDateFrom);
    this.departmentMeEscrowBean.entrustDateTo = this.commfunc.transDateN(this.entrustDateTo);
    this.departmentMeEscrowBean.applyDateFrom = this.commfunc.transDateN(this.applyDateFrom);
    this.departmentMeEscrowBean.applyDateTo = this.commfunc.transDateN(this.applyDateTo);
    this.cusOperationHttpService.myEntrustQryForDepManager(this.departmentMeEscrowBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items1 = data.myEntrustInfo;
        this.total = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  // 解除代管
  dismissEtrust(item) {
    this.confirmDisplay = !this.confirmDisplay
    this.confirmationService.confirm({
      message: `确定要解除代管吗？`,
      accept: () => {
        //条件
        // let param = {};
        ;
        this.cusOperationHttpService.entrustManageCustRelease(this.departmentMeEscrowReleaseBean.entrustApplyId).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.departmentMeEscrowBean.pageNum = 1;
            this.doQuery();
            // this.msgs = [];
            // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "解除代管失败" });
        })
      }
    });
  }
  // 职责查询接口
  queryClick() {
  }

  //代管客户数初始查询
  queryClick1() {
  }
  // 职责数查看
  responNum() {
    this.display = true;
    this.showModel = '1'
  }
  // 职责查看回调
  responseCall(param) {
    this.display = param;
    this.queryClick();
  }

  //代管客户数
  entrustNum() {
    this.display = true;
    this.showModel = '2'
  }

  // 代管客户数查询回掉
  entrustCall(param) {
    this.display = param;
    this.queryClick1()
  }

  //分页
  paginate(event) {
    // ;
    //每页显示的条数
    this.departmentMeEscrowBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.departmentMeEscrowBean.pageNum = currentPage;
    //调用查询的方法
    this.doQuery()
    this.departmentMeEscrowBean.pageNum = 1;
  }
  //日期
  showData() {
    this.temData = this.entrustDateFrom;
    this.temData2 = this.applyDateFrom;
  }
}
